import wave from './wave.vue';
import { wait } from '@/util/commonFun.js';
import { store } from '@/store.js';
export default {
	components: {
		wave
	},
	onLoad() {

		uni.showShareMenu();

	},
	data() {
		return {
			list: [
				{
					icon: 'account',
					text: '我的资料'
				},
				{
					icon: 'share',
					text: '转发给朋友'
				},
				{
					icon: 'feedback',
					text: '意见反馈'
				},
				{
					icon: 'about',
					text: '关于研发中心工作室'
				}
			],
			delay:false
		};
	},
	created() {},
	async mounted() {
		uni.showLoading({
			title: '加载中'
		});
		await wait(300);
		uni.hideLoading();
		this.delay = true;
	},
	computed: {
		// iconColor() {
		// 	return this.$allColor;
		// }
	},
	methods: {
		tapList(icon) {
			switch (icon) {
				case 'account':
					this.$Router.push({
						name: 'information'
					});
					break;
				case 'privacy':
					this.$Router.push({
						name: 'privacy'
					});
					break;
				case 'about':
					this.$Router.push({
						name: 'about'
					});
					break;
			}
		},
	}
};
